body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #4f575e;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.light-text {
  color: #eeeeee;
}

.unlocked {
  color: #17c878;
}

.locked {
  color: #d62b2b;
}

.content-center {
  margin-left: auto;
  margin-right: auto;
}

.content-wrapper {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.clickable {
  cursor: pointer;
}

.completed {
  text-decoration: line-through;
  color: #99b83d;
}

ul.calc-list {
  list-style-type: none;
  columns: 3;
  -webkit-columns: 3;
  -moz-columns: 3;
}

/* Inactive tab */
.tab-bar-dark .nav-link {
  color: #f7f7f7;
}
.tab-bar-dark .nav-link:hover {
  background-color: #6c757d;
}

/* Active tab */
.tab-bar-dark .nav-link.active,
.tab-bar-dark .nav-link.active:hover,
.tab-bar-dark .nav-link.active:focus {
  background-color: #343a40;
  color: #f7f7f7;
  border: 1px solid rgba(0,0,0,.125);
}

/* Disabled tab */
.tab-bar-dark .nav-link.disabled {
  color:  #6c757d;
}

/* Inactive tab */
.tab-bar-secondary .nav-link {
  color: #f7f7f7;
}
.tab-bar-secondary .nav-link:hover {
  background-color: #535c64;
}

/* Active tab */
.tab-bar-secondary .nav-link.active,
.tab-bar-secondary .nav-link.active:hover,
.tab-bar-secondary .nav-link.active:focus {
  background-color: #6c757d;
  color: #f7f7f7;
  border: 1px solid rgba(0,0,0,.125);
}

/* Disabled tab */
.tab-bar-secondary .nav-link.disabled {
  color:  #6c757d;
}

/* Progress bar steps */
.progressStep {
  filter: grayscale(1);
}

.progressStep.accomplished {
  filter: grayscale(0);
}

/* Relic table */
.relic-table {
  background-color: #484e53;
  outline: 1px solid rgba(0,0,0,.125);
}

.relic-table-cell {
  outline: 2px solid rgba(0,0,0,.125);
  margin: 2px;
}

.relic-table-cell.selected {
  outline: 2px solid #a3ce27;
  margin: 2px;
}

.react-bootstrap-table-pagination {
  margin: auto;
  flex-wrap: nowrap;
  flex-direction: row-reverse;
}
